import ReactDOM from 'react-dom';
import React from 'react';
import Snackbar from '@material-ui/core/Snackbar';
import MuiAlert from '@material-ui/lab/Alert';
import { makeStyles } from '@material-ui/core/styles';

import logo from './seshcraft.png';
import './index.css';

class App extends React.Component {
	constructor(props) {
		super(props);
		this.state = {
			callBackSuccess: document.location.href.split('?')[1] === 'success',
			callBackFail: document.location.href.split('?')[1] === 'error'
		};
	}

	render() {
		return (
			<div className="App">
				<Message
					open={this.state.callBackSuccess}
					type={document.location.href.split('?')[1]}
					message="upload completed"
				/>
				<Message
					open={this.state.callBackFail}
					type={document.location.href.split('?')[1]}
					message="upload failed, please try again"
				/>
				<img src={logo} className="App-logo" alt="logo" />
				<form
					action="http://ec2-52-91-0-119.compute-1.amazonaws.com:8080/upload"
					encType="multipart/form-data"
					method="post"
				>
					<input type="file" name="file" />
					<input type="submit" value="upload file" />
				</form>
				<a className="App-link" href="http://ec2-52-91-0-119.compute-1.amazonaws.com:8080/download">
					click here to download current modpack
				</a>
			</div>
		);
	}
}

class Message extends React.Component {
	constructor(props) {
		super(props);
		this.state = { open: this.props.open };
	}

	handleClose = (event, reason) => {
		if (reason === 'clickaway') {
			return;
		}
		this.setState({ open: false });
	};
	render() {
		return (
			<Snackbar
				anchorOrigin={{ vertical: 'top', horizontal: 'center' }}
				open={this.state.open}
				autoHideDuration={5000}
				onClose={() => this.handleClose()}
			>
				<MuiAlert onClose={() => this.handleClose()} severity="success" elevation={6} variant="filled">
					{this.props.message}
				</MuiAlert>
			</Snackbar>
		);
	}
}

ReactDOM.render(<App />, document.getElementById('root'));
